 <template>
  <v-container>
    <v-layout row justify-center align-center>
      <v-card width="100%" flat>
        <v-card-title primary-title>
          <v-flex xs12>
            <h3 class="headline mb-0">  </h3>
            <div :class="{'text-xs-center justify-center align-center':true, 'pa-3':$vuetify.breakpoint.smAndUp}">
              <p>
                <component :is="$vuetify.breakpoint.xsOnly?'v-bottom-sheet':'v-menu'" v-model="bottomSheet" absolute offset-y v-if="profile.picturePath">
                  <v-avatar :size="$vuetify.breakpoint.xsOnly?100:200" class="cursor-pointer" slot="activator" title="    ">
                    <img :src="webUrl+ profile.picturePath" alt=" ">
                  </v-avatar>
                  <v-list>
                    <v-list-tile @click="deleteProfilePath">
                      <v-list-tile-title>  </v-list-tile-title>
                    </v-list-tile>
                    <v-list-tile @click="bottomSheet = false;openDialog()">
                      <v-list-tile-title>  </v-list-tile-title>
                    </v-list-tile>
                  </v-list>
                </component>
                <v-avatar :size="$vuetify.breakpoint.xsOnly?100:200" class="cursor-pointer" @click="openDialog" title="  " v-else>
                  <img :src="require('@/static/img/defaultUser.png')" alt="  ">
                </v-avatar>
              </p>
              <v-form ref="form" lazy-validation>
                <v-container fluid grid-list-xs>
                  <v-layout row wrap>
                    <v-flex sm2 xs4 py-0>
                      <v-subheader @click="focus('loungeNickName')"> </v-subheader>
                    </v-flex>
                    <v-flex sm4 xs8 py-0>
                      <v-text-field ref="loungeNickName" placeholder=" " :rules="nickNameRules" @keydown.enter.stop="submit" @blur="checkNickName('loungeNickName')" v-model="profile.loungeNickName" :error-messages="loungeNickNameErrors" :readonly="!checkLoungeNickNameUpdatable" class="mt-0 pt-0 dense" :hint="checkLoungeNickNameUpdatable?'    .':'   1     .'"></v-text-field>
                    </v-flex>
                    <v-flex sm2 xs4 py-0>
                      <v-subheader @click="focus('topicNickName')"> </v-subheader>
                    </v-flex>
                    <v-flex sm4 xs8 py-0>
                      <v-text-field ref="topicNickName" placeholder=" " :rules="nickNameRules" @keydown.enter.stop="submit" @blur="checkNickName('topicNickName')" v-model="profile.topicNickName" :error-messages="topicNickNameErrors" :readonly="!checkTopicNickNameUpdatable" class="mt-0 pt-0 dense" :hint="checkTopicNickNameUpdatable?'  .':'   1     .'"></v-text-field>
                    </v-flex>
                    <v-flex sm2 xs4 py-0>
                      <v-subheader :style="{'padding-bottom':$vuetify.breakpoint.xsOnly?'12px':false}"></v-subheader>
                    </v-flex>
                    <v-flex sm4 xs8 py-0 text-xs-left>
                      <div class="d-inline-flex align-center fill-height" style="padding-bottom:12px;">
                        <router-link v-if="profile.auth !== 'A'" to="/auth" class="primary--text" title="   ">{{userAuthItems[profile.auth || 'NORMAL']}}</router-link>
                        <span v-else>{{userAuthItems[profile.auth || 'NORMAL']}}</span>
                        <small title=" " v-if="profile.emailVerifiedDate && profile.auth === 'A'">({{$moment(profile.emailVerifiedDate, 'YYYYMMDD').format('Y.M.D')}})</small>
                      </div>
                    </v-flex>
                    <v-flex sm2 xs4 py-0>
                      <v-subheader class="pr-0 text-xs-left" @click="focus('password')"> </v-subheader>
                    </v-flex>
                    <v-flex sm4 xs8 py-0>
                      <v-text-field ref="password" dense placeholder=" " type="password" @keydown.enter.stop="submit" :rules="passwordRules" v-model="password" hint="   ." class="my-0 pt-0 dense"></v-text-field>
                    </v-flex>
                  </v-layout>
                  <v-layout row></v-layout>
                  <v-divider class="my-2"></v-divider>
                  <v-layout>
                    <v-tooltip bottom>
                      <v-switch slot="activator" class="pl-4" label=" " v-model="profile.isOpenInfo"></v-switch>
                      <span>   (, , , , )   .</span>
                    </v-tooltip>
                  </v-layout>
                  <v-layout row align-end>
                    <v-flex xs4>
                      <v-tooltip bottom>
                        <v-text-field slot="activator" v-model="profile.region" label="" class="mt-0 pt-0" disabled hint="NEIS ." placeholder="()" hide-details></v-text-field>
                        <span>NEIS .</span>
                      </v-tooltip>
                    </v-flex>
                    <v-flex xs4>
                      <v-tooltip bottom>
                        <v-select slot="activator" :items="majorItems" v-model="major" label="" class="dense mt-0 pt-0" dense :disabled="!checkProfileUpdatable" hide-details></v-select>
                        <span>{{!checkProfileUpdatable?(isMarch?'  .':' 3  .'):'      .'}}</span>
                      </v-tooltip>
                    </v-flex>
                    <v-flex xs4>
                      <v-tooltip bottom>
                        <v-select slot="activator" :items="gradeItems" v-model="grade" label="" class="dense mt-0 pt-0" dense :disabled="!checkProfileUpdatable" hide-details></v-select>
                        <span>{{!checkProfileUpdatable?(isMarch?'  .':' 3  .'):'      .'}}</span>
                      </v-tooltip>
                    </v-flex>
                  </v-layout>
                </v-container>
              </v-form>
            </div>
          </v-flex>
        </v-card-title>
        <v-card-actions pa-3>
          <v-layout :row="$vuetify.breakpoint.smAndUp" :column="$vuetify.breakpoint.xsOnly" text-xs-right>
            <v-btn @click="reset" flat class="mt-2"></v-btn>
            <template v-if="$vuetify.breakpoint.smAndUp">
              <v-flex class="mt-2">
                <v-subheader>
                  <v-btn @click="exit" class="short" flat>
                    <small></small>
                  </v-btn>
                </v-subheader>
              </v-flex>
              <v-flex>
                <v-spacer></v-spacer>
              </v-flex>
            </template>
            <v-flex class="mt-2">
              <v-btn @click="submit" color="primary" :loading="loading" :block="$vuetify.breakpoint.xsOnly"></v-btn>
            </v-flex>
          </v-layout>
        </v-card-actions>
      </v-card>
      <v-dialog v-model="dialog" width="500" lazy content-class="overflow-hidden">
        <v-card>
          <v-card-title class="headline" primary-title>
            <span>{{profile.picturePath? '  ':'  '}}</span>
            <v-spacer/>
            <v-icon @click="dialog = false">close</v-icon>
          </v-card-title>
          <v-card-text>
            <file-pond name="picture" ref="pond" :server="server" accepted-file-types="image/*"/>
          </v-card-text>
        </v-card>
      </v-dialog>
      <v-layout v-show="dialog"></v-layout>
    </v-layout>
  </v-container>
</template>

<script>
import MainLayout from '../layouts/MainLayout';

import 'filepond-polyfill';
import vueFilePond, { setOptions } from 'vue-filepond';
import FilePondPluginFileValidateType from 'filepond-plugin-file-validate-type/dist/filepond-plugin-file-validate-type.esm.js';
const FilePond = vueFilePond(FilePondPluginFileValidateType);
setOptions({
  labelIdle: '     .(400KB )',
  labelFileWaitingForSize: '  ...',
  labelFileSizeNotAvailable: '    .',
  labelFileLoading: '  ...',
  labelFileLoadError: '  .',
  labelFileProcessing: ' ...',
  labelFileProcessingComplete: '  .',
  labelFileProcessingAborted: ' .',
  labelFileProcessingError: '  .',
  labelTapToCancel: '',
  labelTapToRetry: '',
  labelTapToUndo: '',
  labelButtonRemoveItem: '',
  labelButtonAbortItemLoad: '',
  labelButtonRetryItemLoad: '',
  labelButtonAbortItemProcessing: '',
  labelButtonUndoItemProcessing: '',
  labelButtonProcessItem: '',
  labelFileTypeNotAllowed: '   .',
  fileValidateTypeLabelExpectedTypes: 'jpg, png, gif, png     .',
  allowRevert: false
});

export default {
  name: 'MyPage',
  components: {
    FilePond
  },
  data () {
    return {
      loading: false,
      dialog: false,
      grade: null,
      major: null,
      password: null,
      gradeItems: [],
      majorItems: [],
      loungeNickNameErrors: [],
      topicNickNameErrors: [],
      passwordRules: [v => !v || (v.length > 3 && v.length < 26) || '4~25'],
      nickNameRules: [v => (!!v && v !== '') || '/ .', v => (!!v && v.length > 3 && v.length <= 50) || '4~50 .'],
      bottomSheet: false,
      server: {
        process: (fieldName, file, metadata, load, error, progress, abort) => {
          if (file.size > 400 * 1024) {
            this.$store.dispatch('showSnackbar', { text: ' 400KB    .', color: 'error' });
            abort();
            return;
          }
          const formData = new FormData();
          formData.append('picture', file, file.name);
          this.$axios
            .post('/user/picture', formData)
            .then(response => {
              this.profile.picturePath = response.data.picturePath;
              this.dialog = false;
              this.$store.dispatch('updateProfile', { picturePath: this.profile.picturePath });
              load();
            })
            .catch(error => {
              abort();
              this.$store.dispatch('showSnackbar', { text: `${error.response ? error.response.data.message : '   .'}`, color: 'error' });
            });
          return { load, error, progress, abort };
        }
      },
      labels: {},
      userAuthItems: {
        N: '()',
        A: '',
        E: '()',
        D: ''
      }
    };
  },
  computed: {
    isMarch () {
      return process.env.NODE_ENV === 'development' || new Date().getMonth() === 2;
    },
    checkProfileUpdatable () {
      return (!this.profile.infoModifiedDate || this.$moment(this.profile.infoModifiedDate, 'YYYYMMDD').year() < new Date().getYear()) && this.isMarch;
    },
    checkLoungeNickNameUpdatable () {
      return (
        !this.profile.loungeNickNameModifiedDate ||
        this.$moment(this.profile.loungeNickNameModifiedDate, 'YYYYMMDD')
          .add(1, 'months')
          .isBefore(this.$moment())
      );
    },
    checkTopicNickNameUpdatable () {
      return (
        !this.profile.topicNickNameModifiedDate ||
        this.$moment(this.profile.topicNickNameModifiedDate, 'YYYYMMDD')
          .add(1, 'months')
          .isBefore(this.$moment())
      );
    },
    webUrl () {
      return process.env.VUE_APP_WEB_URL;
    },
    profile () {
      return Object.assign({}, this.$store.getters.profile);
    }
  },
  created () {
    this.$emit('update:layout', MainLayout);
    this.$store.dispatch('setColumnType', 'HIDE_ALWAYS');
    this.reset();
    this.$axios
      .get('/group', { params: { groupType: ['M', 'G'] }, headers: { silent: true } })
      .then(response => {
        if (this.checkProfileUpdatable) {
          this.majorItems = response.data.filter(x => x.groupType === 'M').map(x => ({ text: x.groupName, value: x.groupId }));
          this.majorItems.splice(0, 0, { text: '()', value: null });
          this.gradeItems = response.data.filter(x => x.groupType === 'G').map(x => ({ text: x.groupName, value: x.groupId }));
          this.gradeItems.splice(0, 0, { text: '()', value: null });
        } else {
          this.majorItems = response.data.filter(x => x.groupType === 'M' && x.groupId === this.profile.major).map(x => ({ text: x.groupName, value: x.groupId }));
          this.majorItems.splice(0, 0, { text: '()', value: null });
          this.gradeItems = response.data.filter(x => x.groupType === 'G' && x.groupId === this.profile.grade).map(x => ({ text: x.groupName, value: x.groupId }));
          this.gradeItems.splice(0, 0, { text: '()', value: null });
        }
      })
      .catch(error => {
        console.log(error);
        this.$store.dispatch('showSnackbar', { text: `,    .${error && error.response && error.response.data ? '[' + error.response.data.message + ']' : ''}`, color: 'error' });
      });
  },
  methods: {
    reset () {
      if (this.profile.major === undefined) {
        this.major = null;
      } else {
        this.major = this.profile.major;
      }
      if (this.profile.grade === undefined) {
        this.grade = null;
      } else {
        this.grade = this.profile.grade;
      }
      this.password = null;
    },
    checkNickName (target, callback) {
      let errors, nickName;
      switch (target) {
        case 'loungeNickName':
          errors = this.loungeNickNameErrors;
          nickName = this.profile.loungeNickName;
          this.$refs.loungeNickName.validate();
          break;
        case 'topicNickName':
          errors = this.topicNickNameErrors;
          nickName = this.profile.topicNickName;
          this.$refs.topicNickName.validate();
          break;
        default:
          if (typeof callback === 'function') {
            callback.call(this);
          }
          return;
      }
      if (errors.length === 0 && nickName !== this.$store.getters.profile[target]) {
        this.$axios
          .get('/nickName', { params: { nickName: nickName }, headers: { silent: true } })
          .then(() => {
            if (typeof callback === 'function') {
              callback.call(this);
            }
          })
          .catch(error => {
            errors.push(error.response ? error.response.data.message || '   /.' : '   /.');
            if (typeof callback === 'function') {
              callback.call(this);
            }
          });
      } else if (typeof callback === 'function') {
        callback.call(this);
      }
    },
    focus (target) {
      if (this.$refs[target]) {
        this.$refs[target].focus();
      }
    },
    submit () {
      if (this.$refs.form.validate()) {
        this.checkNickName('topicNickName', () => {
          this.checkNickName('loungeNickName', () => {
            if (this.loungeNickNameErrors.length === 0 && this.topicNickNameErrors.length === 0) {
              let params = {};
              const origin = this.$store.getters.profile;
              if (this.profile.topicNickName !== origin.topicNickName) {
                this.checkNickName('topicNickName');
                params.topicNickName = this.profile.topicNickName;
              }
              if (this.profile.loungeNickName !== origin.loungeNickName) {
                this.checkNickName('loungeNickName');
                params.loungeNickName = this.profile.loungeNickName;
              }
              if (this.profile.isOpenInfo !== origin.isOpenInfo) {
                params.isOpenInfo = this.profile.isOpenInfo;
              }
              if (this.major !== origin.major && !(this.major === null && origin.major === undefined) && this.checkProfileUpdatable) {
                params.major = this.major;
              }
              if (this.grade !== origin.grade && !(this.grade === null && origin.grade === undefined) && this.checkProfileUpdatable) {
                params.grade = this.grade;
              }
              if (this.password && this.password !== '') {
                params.password = this.password;
              }
              if (Object.keys(params).length > 0) {
                if (params.major !== undefined || params.grade !== undefined) {
                  if (!confirm(` ${params.major && this.majorItems.find(x => x.value === params.major) ? this.majorItems.find(x => x.value === params.major).text + ' ' : ''},  ${params.grade && this.gradeItems.find(x => x.value === params.grade) ? this.gradeItems.find(x => x.value === params.grade).text + ' ' : ''}?\n      ,  .`)) {
                    return;
                  }
                }
                this.loading = true;
                this.$axios
                  .put('/user', params)
                  .then(response => {
                    this.password = null;
                    this.loading = false;
                    this.$store.dispatch('showSnackbar', { text: '  .', color: 'success' });
                    if (params.major !== undefined || params.grade !== undefined) {
                      params.infoModifiedDate = this.$moment().format('YMMDD');
                      this.profile.infoModifiedDate = params.infoModifiedDate;
                    }
                    if (params.topicNickName) {
                      params.topicNickNameModifiedDate = this.$moment().format('YMMDD');
                      this.profile.topicNickNameModifiedDate = params.topicNickNameModifiedDate;
                    }
                    if (params.loungeNickName) {
                      params.loungeNickNameModifiedDate = this.$moment().format('YMMDD');
                      this.profile.loungeNickNameModifiedDate = params.loungeNickNameModifiedDate;
                    }
                    this.$store.dispatch('updateProfile', params);
                  })
                  .catch(error => {
                    this.loading = false;
                    this.$store.dispatch('showSnackbar', { text: `  .${error.response ? '[' + error.response.data.message + ']' : ''}`, color: 'error' });
                    console.log(error);
                  });
              } else {
                this.$store.dispatch('showSnackbar', { text: '  .' });
              }
            }
          });
        });
      } else {
        this.$store.dispatch('showSnackbar', { text: '  .', color: 'error' });
      }
    },
    deleteProfilePath () {
      this.$axios
        .put('/user', { picturePath: '' })
        .then(response => {
          this.$store.dispatch('updateProfile', { picturePath: null });
          this.profile.picturePath = null;
        })
        .catch(error => {
          console.log(error);
          this.$store.dispatch('showSnackbar', { text: `${error.response ? error.response.data.message : '   .'}`, color: 'error' });
        });
      this.bottomSheet = false;
    },
    exit () {
      if (confirm(' ?\n   .')) {
        this.$axios
          .put('/user', { status: 'DELETED' })
          .then(response => {
            this.$store.dispatch('showSnackbar', { text: ' .  .' });
            this.$store.dispatch('signout');
            this.$router.push('/index');
          })
          .catch(error => {
            console.log(error);
            this.$store.dispatch('showSnackbar', { text: `${error.response ? error.response.data.message : ' .  .'}`, color: 'error' });
          });
      }
    },
    openDialog () {
      this.dialog = true;
      if (this.$refs.pond) {
        this.$refs.pond.browse();
      } else {
        let check = setInterval(() => {
          if (this.$refs.pond) {
            clearInterval(check);
            this.$refs.pond.browse();
          }
        }, 100);
      }
    }
  },
  watch: {
    'profile.loungeNickName' () {
      if (this.loungeNickNameErrors.length > 0) {
        this.loungeNickNameErrors = [];
      }
    },
    'profile.topicNickName' () {
      if (this.topicNickNameErrors.length > 0) {
        this.topicNickNameErrors = [];
      }
    }
  }
};
</script>
